<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">
        <a-form-item
                label='表决标题'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="title"
        >
            <a-input/>
        </a-form-item>
        <a-form-item
                label='表决内容'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="initiate_content"
        >
            <a-textarea :autosize="{ minRows: 2, maxRows: 6 }"/>
        </a-form-item>
        <a-form-item
                label='结果公开'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                :fieldDecoratorOptions="{valuePropName:'checked'}"
                fieldDecoratorId="is_publicized"
        >
            <a-radio-group>
                <a-radio-button :value="1">是</a-radio-button>
                <a-radio-button :value="0">否</a-radio-button>
            </a-radio-group>
        </a-form-item>
        <a-form-item
                label='开始时间'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="started_at"
        >
            <DateTime/>
        </a-form-item>
        <a-form-item
                label='结束时间'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="ended_at"
        >
            <DateTime/>
        </a-form-item>
        <a-form-item
                label='表决参与人'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="attend_user_ids"
        >
            <SelectUsers @change="(val)=>{
            this.log(val)
             this.form.setFieldsValue({
                attend_user_ids: val,
                        })
            }"/>
        </a-form-item>
        <a-form-item
                label='附件'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="more_files"
        >
            <upload-files :length="3"/>
        </a-form-item>
        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getVote, createVote, updateVote} from '@/api/vote'
    import DateTime from '@/components/select/DateTime'
    import SelectUser from '@/components/select/SelectUser'
    import SelectUsers from '@/components/select/SelectUsers'
    import uploadFiles from '@/components/upload/UploadFiles'

    export default {
        components: {
            DateTime, SelectUser, SelectUsers, uploadFiles
        },
        props: {
            id: {
                type: Number,
                default: 0,
            }
        },
        data() {
            return {
                log: console.log,
                form: {},
            }
        },
        computed: {},
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getVote(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            title: res.data.title,
                            initiate_content: res.data.initiate_content,
                            is_publicized: res.data.is_publicized,
                            started_at: res.data.started_at,
                            ended_at: res.data.ended_at,
                            attend_user_ids: res.data.attend_user_ids,
                            more_files: res.data.more.hasOwnProperty('files') ? res.data.more.files : null,
                        })
                    })
            },
            beforeSubmit() {
                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;

                let data = this.form.getFieldsValue();

                if (this.id) {
                    request = updateVote(this.id, data)
                } else {
                    request = createVote(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        },
    }
</script>
